<template>
  <div class="signup-page">
    <div class="container">
      <h1>회원가입</h1>  
      <form id="signupForm" @submit.prevent="submitForm">

        <div class="input-group">
          <label for="name" :class="{ active: user.username || nameIsFocused }" class="required-field">이름</label>
          <input 
            type="text" 
            id="name" 
            v-model="user.username" 
            required
            @focus="nameIsFocused = true"
            @blur="nameIsFocused = false; nameTouched = true"
            :class="{ error: !isNameValid && nameTouched, valid: isNameValid && nameTouched }"
          >
        </div>
        
        <div class="input-group">
          <label for="email" :class="{ active: user.email || emailIsFocused }" class="required-field">이메일</label>
          <input 
            type="email" 
            id="email" 
            v-model="user.email" 
            required
            @focus="emailIsFocused = true"
            @blur="emailIsFocused = false; emailTouched = true"
            :class="{ error: !isEmailValid && emailTouched, valid: isEmailValid && emailTouched }"
          >
        </div>
        
        <div class="input-group">
          <label for="password" :class="{ active: user.password || passwordIsFocused }" class="required-field">비밀번호</label>
          <input 
            type="password" 
            id="password" 
            v-model="user.password" 
            required
            @focus="showPasswordRequirements = true; passwordIsFocused = true"
            @blur="passwordIsFocused = false; passwordTouched = true"
            :class="{ error: !isPasswordValid && passwordTouched, valid: isPasswordValid && passwordTouched }"
          >
          <div class="pwd-requirements" :class="{ show: showPasswordRequirements && user.password !== '' }">
            <div class="requirement" :class="{ valid: isLengthValid }">
              <span class="requirement-icon">{{ isLengthValid ? '✓' : '✕' }}</span>
              <span>최소 8자를 포함해야 합니다</span>
            </div>
            <div class="requirement" :class="{ valid: isCharsValid }">
              <span class="requirement-icon">{{ isCharsValid ? '✓' : '✕' }}</span>
              <span>대문자, 소문자, 숫자를 포함해야 합니다</span>
            </div>
            <div class="requirement" :class="{ valid: isSpecialValid }">
              <span class="requirement-icon">{{ isSpecialValid ? '✓' : '✕' }}</span>
              <span>12자 미만인 경우 특수 문자를 포함해야 합니다</span>
            </div>
          </div>
        </div>
        
        <div class="input-group">
          <label for="confirm-password" :class="{ active: confirmPassword || confirmPasswordIsFocused }" class="required-field">비밀번호 재확인</label>
          <input 
            type="password" 
            id="confirm-password" 
            v-model="confirmPassword" 
            required
            @focus="confirmPasswordIsFocused = true"
            @blur="confirmPasswordIsFocused = false; confirmPasswordTouched = true"
            :class="{ error: !passwordsMatch && confirmPasswordTouched, valid: passwordsMatch && confirmPasswordTouched }"
          >
        </div>
        
        <div class="terms-section">
          <div class="terms-container">
            <div class="terms-title">
              <span>약관 전체 동의</span>
            </div>
            <div class="terms-checkbox">
              <input type="checkbox" id="terms-all" v-model="termsAll" @change="updateTerms">
              <label for="terms-all">모든 약관 동의</label>
            </div>
          </div>
          
          <div class="terms-container" v-for="(term, index) in terms" :key="index">
            <div class="terms-title" @click="toggleTerms(index)">
              <span>{{ term.title }}&nbsp;&nbsp;&nbsp;&nbsp;</span>
              <span :class="term.required ? 'required-tag' : 'optional-tag'">{{ term.required ? '(필수)' : '(선택)' }}</span>
              <span class="toggle-icon" :class="{ rotated: term.show }">▼</span>
            </div>
            <div class="terms-content" :class="{ show: term.show }">
              <p v-for="(paragraph, pIndex) in term.content" :key="pIndex">{{ paragraph }}</p>
            </div>
            <div class="terms-checkbox">
              <input 
                type="checkbox" 
                :id="'terms' + (index + 1)" 
                v-model="term.agreed" 
                @change="updateTermsAllCheckbox"
                :required="term.required"
              >
              <label :for="'terms' + (index + 1)">동의</label>
            </div>
          </div>
        </div>
        
        <button type="submit" :disabled="!isFormValid">회원가입</button>
      </form>
    </div>
  </div>  
</template>

<script>
import axiosInstance from '@/axios_config/axiosConfig';

export default {
  data() {
    return {
      user:{
        username: '',
        email: '',
        password: '',
      },
      confirmPassword: '',
      
      nameIsFocused: false,
      emailIsFocused: false,
      passwordIsFocused: false,
      confirmPasswordIsFocused: false,
      
      nameTouched: false,
      emailTouched: false,
      passwordTouched: false,
      confirmPasswordTouched: false,
      
      showPasswordRequirements: false,
      termsAll: false,


      terms: [
        {
          title: '홈페이지 이용약관 동의',
          required: true,
          show: false,
          agreed: false,
          content: [
            '제1조 (목적) 이 약관은 회사가 제공하는 서비스의 이용과 관련하여 회사와 회원과의 권리, 의무 및 책임사항, 기타 필요한 사항을 규정함을 목적으로 합니다.',
            '제2조 (정의) 이 약관에서 사용하는 용어의 정의는 다음과 같습니다. 1. "서비스"라 함은 회사가 제공하는 모든 서비스를 의미합니다. 2. "회원"이라 함은 회사와 서비스 이용계약을 체결하고 회사가 제공하는 서비스를 이용하는 고객을 말합니다.'
          ]
        },
        {
          title: '개인정보 수집 및 이용동의',
          required: true,
          show: false,
          agreed: false,
          content: [
            '1. 수집하는 개인정보 항목: 이름, 이메일, 비밀번호, 나이, 성별',
            '2. 수집 및 이용목적: 회원제 서비스 제공, 서비스 이용에 따른 본인확인, 불만처리 등 민원처리',
            '3. 보유 및 이용기간: 회원 탈퇴 시까지 (법령에 따로 정한 경우 해당 기간)'
          ]
        },
        {
          title: '마케팅 활용 수집 이용동의',
          required: false,
          show: false,
          agreed: false,
          content: [
            '1. 수집하는 개인정보 항목: 이름, 이메일',
            '2. 수집 및 이용목적: 마케팅 및 광고에 활용, 이벤트 및 프로모션 안내',
            '3. 보유 및 이용기간: 회원 탈퇴 시까지 또는 마케팅 동의 철회 시까지'
          ]
        }
      ]
    };
  },
  
  computed: {
    isNameValid() {
      return this.user.username !== '';
    },
    
    isEmailValid() {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(this.user.email);
    },
    
    isLengthValid() {
      return this.user.password.length >= 8;
    },
    
    isCharsValid() {
      return /[A-Z]/.test(this.user.password) && 
             /[a-z]/.test(this.user.password) && 
             /[0-9]/.test(this.user.password);
    },
  
    isSpecialNeeded() {
      return this.user.password.length < 12;
    },
    
    hasSpecialChar() {
      // eslint-disable-next-line no-useless-escape
      return /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/.test(this.user.password);
    },
    
    isSpecialValid() {
      return !this.isSpecialNeeded || this.hasSpecialChar;
    },
    
    isPasswordValid() {
      if (this.user.password === '') return false;
      return this.isLengthValid && this.isCharsValid && this.isSpecialValid;
    },
    
    passwordsMatch() {
      return this.user.password !== '' && this.user.password === this.confirmPassword;
    },
    
    areRequiredTermsAgreed() {
      return this.terms.filter(term => term.required).every(term => term.agreed);
    },
    
    isFormValid() {
      return this.isNameValid && 
             this.isEmailValid && 
             this.isPasswordValid && 
             this.passwordsMatch && 
             this.areRequiredTermsAgreed;
    }
  },
  
  methods: {
    toggleTerms(index) {
      this.terms[index].show = !this.terms[index].show;
    },
    
    updateTerms() {
      this.terms.forEach(term => {
        term.agreed = this.termsAll;
      });
    },
    
    updateTermsAllCheckbox() {
      this.termsAll = this.terms.every(term => term.agreed);
    },
    
    async submitForm() {
      if (this.isFormValid) {
        try {
          const response = await axiosInstance.post('/user/register', {
                    username: this.user.username,
                    email: this.user.email,
                    password: this.user.password,
                });
                console.log(response.data);
                 if (response.data.status_code === 200) {
                     this.$router.push('/login');
                 } else {
                     console.error('Failed register');
                 }
             } catch (error) {
                 console.error('Error register:', error);
             }
          }
        }
      }
    };
    
</script>

