import { createRouter, createWebHashHistory } from 'vue-router'
import routeHome from '@/components/route/routeHome.vue'
import routeLive from '@/components/route/routeLive.vue'
import routeStudio from '@/components/route/routeStudio.vue'
import routeLogin from '@/components/route/routeLogin.vue'
import routeSettings from '@/components/route/routeSettings.vue'
import routeSignUp from '@/components/route/routeSignUp.vue'
import routeFindPassword from '@/components/route/routeFindPassword.vue'
import routePasswordchange from '@/components/route/routePasswordchange.vue'
import HighlightAnalysis from '@/components/route/HighlightAnalysis.vue'
import ChatAnalysis from '@/components/route/ChatAnalysis.vue'
// import ChannelSummary from '@/components/analysis/ChannelSummary.vue'
import store from '@/store'
import routeAichatbot from '@/components/route/routeAichatbot.vue'
import routeNormalAnalysis from '@/components/analysis/routeNormalAnalysis.vue'
const routes = [
  {path:'', name:'home', component:routeHome, meta: { requiresAuth: false }},
  {
    path:'/live/:liveid', 
    name:'live', 
    component:routeLive, 
    meta: { 
      requiresAuth: true,
      title: '라이브'
    }
  },
  {
    path:'/studio/:channelId', // /:channelId
    name:'studio',
    component:routeStudio,
    children: [
      {
        path: '',
        name: 'studio-default',
        component: routeNormalAnalysis,
        meta: { 
          requiresAuth: true,
          title: '스튜디오'
        }
      },
      {
        path: 'chat',
        name: 'studio-chat',
        component: ChatAnalysis,
        meta: { 
          requiresAuth: true,
          title: '채팅 분석'
        }
      },
      {
        path: 'highlight',
        name: 'studio-highlight',
        component: HighlightAnalysis,
        meta: { 
          requiresAuth: true,
          title: '하이라이트 분석'
        }
      }
    ],
    meta: { 
      requiresAuth: true,
      title: '스튜디오'
    }
  },
  {
    path:'/login', 
    name:'login', 
    component:routeLogin,
    meta: { requiresAuth: false, noAuthRequired: true }
  },
  {
    path:'/settings', 
    name:'settings', 
    component:routeSettings, 
    meta: { 
      requiresAuth: true,
      title: '설정'
    }
  },
  {
    path:'/signup', 
    name:'signup', 
    component:routeSignUp,
    meta: { requiresAuth: false, noAuthRequired: true }
  },
  {
    path:'/findpassword', 
    name:'findpassword', 
    component:routeFindPassword,
    meta: { requiresAuth: false, noAuthRequired: true }
  },
  {
    path:'/passwordchange', 
    name:'passwordchange', 
    component:routePasswordchange,
    meta: { requiresAuth: true }
  },
  {
    path:'/chatting',
    name:'chatting',
    component:routeAichatbot,
    meta: { requiresAuth: true }
  }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

// 네비게이션 가드 설정
router.beforeEach(async (to, from, next) => {
  // 모든 페이지에서 로그인 상태 확인
  await store.dispatch('checkSession');

  // 로그인 페이지인 경우
  if (to.meta.noAuthRequired) {
    if (store.getters.isAuthenticated) {
      // 이미 로그인한 사용자는 홈으로 리다이렉트
      next({ path: '/' });
    } else {
      next();
    }
    return;
  }

  // 인증이 필요한 페이지인 경우
  if (to.meta.requiresAuth) {
    if (!store.getters.isAuthenticated) {
      // 로그인이 필요한 페이지에 로그인하지 않은 사용자가 접근할 경우
      // const pageTitle = to.meta.title ? `${to.meta.title} ` : '';
      // alert(`${pageTitle}페이지는 로그인이 필요한 서비스입니다.`);
      next({
        path: '/login',
        query: { redirect: to.fullPath }
      });
    } else {
      next();
    }
  } else {
    // 인증이 필요하지 않은 페이지는 그대로 진행
    next();
  }
});

export default router
