<template>
    <div class="analysis-section">
        <div class="section-header">
            <h2>채팅 수 추이</h2>
            <div class="date-filter">
                <SelectLive :liveList="liveHistory" @selected="handleSelectedLive" />
                <select v-model="selectedChatHistoryPeriod" @change="fetchChatHistory">
                    <option value="10min">10분</option>
                    <option value="30min">30분</option>
                    <option value="hour">시</option>
                    <option value="day">일</option>
                    <option value="month">월</option>
                    <option value="year">연</option>
                    <option value="all">전체</option>
                </select>
            </div>
        </div>
        <div class="chat-history-chart-container">
            <canvas id="chatTrendChart"></canvas>
        </div>
    </div>
</template>

<script>
import axiosInstance from '@/axios_config/axiosConfig';
import SelectLive from '../../common/selectLive.vue';
import Chart from 'chart.js/auto';
export default {
    name: 'ChatHistory',
    components: {
        SelectLive
    },
    props: {
        channelId: {
            type: String,
            required: true
        },
        liveHistory: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            selectedChatHistoryPeriod: '10min',
            chatHistory: [],
            selectedLiveId: '',
            chatTrendChartInstance: null,
        }
    },
    computed: {
        chatTrendData() {
            if (!this.chatHistory || this.chatHistory.length === 0) {
                return {
                    labels: [],
                    dataCounts: []
                };
            }
            return {
                labels: this.chatHistory.map(item => {
                    if (item._id && typeof item._id === 'string') {
                        return item._id.includes('_') ? item._id.split('_').pop() + '시' : item._id;
                    }
                    return 'N/A';
                }),
                dataCounts: this.chatHistory.map(item => item.chat_count || 0)
            };
        }
    },
    mounted() {
        if (this.liveHistory && this.liveHistory.length > 0 && !this.selectedLiveId) {
            // Optionally select the first live by default or handle no selection
        }
        this.fetchChatHistory();
    },
    methods: {
        handleSelectedLive(selectedId) {
            this.selectedLiveId = selectedId;
            if (this.selectedLiveId) {
                this.fetchChatHistory();
            } else {
                this.chatHistory = [];
                if (this.chatTrendChartInstance) {
                    this.chatTrendChartInstance.destroy();
                    this.chatTrendChartInstance = null;
                }
            }
        },
        async fetchChatHistory() {
            try {
                const response = await axiosInstance.get(`/chat/analytics/chat_history`, {
                    params: {
                        channel_id: this.channelId,
                        live_key: this.selectedLiveId,
                        period: this.selectedChatHistoryPeriod
                    }
                });
                // console.log('API 응답:', response.data.content.chat_history);
                this.chatHistory = response.data.content.chat_history || [];
                // console.log('this.chatHistory:', this.chatHistory);
                this.drawChatTrendChart();
            } catch (error) {
                // console.error('채팅 이력 로딩 실패:', error);
                this.chatHistory = [];
                if (this.chatTrendChartInstance) {
                    this.chatTrendChartInstance.destroy();
                    this.chatTrendChartInstance = null;
                }
            }
        },
        drawChatTrendChart() {
            // console.log('drawChatTrendChart 호출됨');
            // console.log('this.chatTrendData:', this.chatTrendData);

            if (this.chatTrendChartInstance) {
                this.chatTrendChartInstance.destroy();
            }
            
            const ctx = document.getElementById('chatTrendChart').getContext('2d');
            
            this.chatTrendChartInstance = new Chart(ctx, {
                type: 'line',
                data: {
                    labels: this.chatTrendData.labels,
                    datasets: [{
                        label: '채팅 수',
                        data: this.chatTrendData.dataCounts,
                        borderColor: '#8CF064',
                        backgroundColor: 'rgba(140, 240, 100, 0.1)',
                        tension: 0.4,
                        fill: true,
                        pointRadius: 4,
                        pointBackgroundColor: '#8CF064'
                    }]
                },
                options: {
                    responsive: true,
                    maintainAspectRatio: false,
                    plugins: {
                        legend: { display: false, position: 'top', labels: {color: '#bbb'} },
                        tooltip: {
                            callbacks: {
                                label: function(context) {
                                    return `채팅 수: ${context.raw.toLocaleString()}개`;
                                }
                            }
                        }
                    },
                    scales: {
                        y: {
                            beginAtZero: true,
                            grid: { color: '#333' },
                            ticks: { 
                                color: '#bbb',
                                callback: function(value) {
                                    return value.toLocaleString();
                                }
                            }
                        },
                        x: {
                            grid: { color: '#333' },
                            ticks: { color: '#bbb' }
                        }
                    }
                }
            });
        },
        formatTime(timestamp) {
            const date = new Date(timestamp);
            return date.toLocaleString('ko-KR', {
                year: 'numeric',
                month: '2-digit',
                day: '2-digit',
                hour: '2-digit',
                minute: '2-digit',
                second: '2-digit'
            });
        },
    }
}
</script>

<style scoped>
@import '@/assets/chat-analysis-common.css';
</style> 