<template>
    <div class="analysis-section">
        <div class="section-header">
            <h2>방송별 감정 추이</h2>
            <div class="filter-group">
                <div class="date-picker">
                    <input type="date" v-model="selectedTrendDate" @change="fetchTrendData">
                </div>
                <div class="date-filter">
                    <select v-model="selectedTrendPeriod" @change="fetchTrendData">
                        <option value="hour">시</option>
                        <option value="day">일</option>
                        <option value="month">월</option>
                        <option value="year">연</option>
                        <option value="all">전체</option>
                    </select>
                </div>
            </div>
        </div>
        <div class="emotion-trend-chart">
            <canvas id="emotionTrendChart"></canvas>
        </div>
    </div>
</template>

<script>
import Chart from 'chart.js/auto';
import axiosInstance from '@/axios_config/axiosConfig';
export default {
    name: 'EmotionTrend',
    props: {
        channelId: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            trendData: [],
            selectedTrendPeriod: 'hour',
            selectedTrendDate: new Date().toISOString().split('T')[0],
        }
    },
    mounted() {
        this.fetchTrendData();
    },
    methods: {
        async fetchTrendData() {
            try {
                const response = await axiosInstance.get(`/chzzk/${this.channelId}/emotion-trend`, {
                    params: {
                        period: this.selectedTrendPeriod,
                        date: this.selectedTrendDate
                    }
                });
                this.trendData = response.data.content.trendData;
                this.drawTrendChart();
            } catch (error) {
                console.error('감정 추이 데이터 로딩 실패:', error);
            }
        },
        drawTrendChart() {
            const existingChart = Chart.getChart('emotionTrendChart');
            if (existingChart) existingChart.destroy();
            const ctx = document.getElementById('emotionTrendChart').getContext('2d');
            new Chart(ctx, {
                type: 'line',
                data: {
                    labels: this.trendData.dates,
                    datasets: [
                        {
                            label: '긍정',
                            data: this.trendData.positive,
                            borderColor: '#4caf50',
                            backgroundColor: 'rgba(76,175,80,0.08)',
                            tension: 0.4,
                            pointRadius: 4,
                            pointBackgroundColor: '#4caf50',
                            fill: false
                        },
                        {
                            label: '부정',
                            data: this.trendData.negative,
                            borderColor: '#e74c3c',
                            backgroundColor: 'rgba(231,76,60,0.08)',
                            tension: 0.4,
                            pointRadius: 4,
                            pointBackgroundColor: '#e74c3c',
                            fill: false
                        },
                        {
                            label: '중립',
                            data: this.trendData.neutral,
                            borderColor: '#9e9e9e',
                            backgroundColor: 'rgba(158,158,158,0.08)',
                            tension: 0.4,
                            pointRadius: 4,
                            pointBackgroundColor: '#9e9e9e',
                            fill: false
                        }
                    ]
                },
                options: {
                    maintainAspectRatio: false,
                    responsive: true,
                    plugins: {
                        legend: { 
                            display: true, 
                            position: 'top',
                            onClick: null,
                            labels: { 
                                color: '#bbb',
                                usePointStyle: true,
                                pointStyle: 'line',
                                pointStyleWidth: 20,
                                padding: 20,
                                font: {
                                    size: 14,
                                    weight: '500'
                                }
                            }
                        }
                    },
                    scales: {
                        x: {
                            grid: { color: '#333' },
                            ticks: { color: '#bbb' }
                        },
                        y: {
                            beginAtZero: true,
                            grid: { color: '#333' },
                            ticks: { color: '#bbb' }
                        }
                    }
                }
            });
        }
    }
}
</script>

