<template>
  <div class="highlight-analysis">
    <div class="left-section">
      <div class="time-selector">
        <SelectLive :liveList="liveHistory" @selected="handleSelectedLive" />
        <select v-model="period" @change="fetchHighlightData">
          <option value="10min">10분</option>
          <option value="30min">30분</option>
          <option value="hour">시</option>
          <option value="day">일</option>
          <option value="month">월</option>
          <option value="year">연</option>
          <option value="all">전체</option>
        </select>
      </div>
      <div class="graph-container" ref="graphContainer"></div>
    </div>
    <div class="right-section">
      <div class="highlight-blocks">
        <div v-for="(highlight, index) in highlightData" :key="index" 
             class="highlight-block" 
             :class="{ 'expanded': selectedHighlightIndex === index }"
             @click="toggleHighlight(index)">
          <div class="block-header">
            <span class="block-time">{{ formatTime(highlight._id) }}</span>
            <span class="block-count">{{ highlight.count }}회</span>
          </div>
          <div class="block-details" v-if="selectedHighlightIndex === index">
            <div class="detail-item">
              <span class="label">하이라이트 발생:</span>
              <span class="value">{{ highlight.count }}회</span>
            </div>
            <div class="detail-item">
              <span class="label">평균 채팅 수:</span>
              <span class="value">{{ highlight.avg_chat_count?.toFixed(2) }}</span>
            </div>
            <div class="detail-item">
              <span class="label">평균 upper_band:</span>
              <span class="value">{{ highlight.avg_upper_band?.toFixed(2) }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axiosInstance from '@/axios_config/axiosConfig';
import * as echarts from 'echarts';
import SelectLive from '../../common/selectLive.vue';

export default {
  name: 'HighlightAnalysis',
  components: {
    SelectLive
  },
  props: {
    liveHistory: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      period: 'hour',
      chart: null,
      selectedHighlight: null,
      selectedLiveId: null,
      highlightData: [],
      selectedHighlightIndex: null
    }
  },
  methods: {
    async handleSelectedLive(selectedId) {
      this.selectedLiveId = selectedId;
      await this.fetchHighlightData();
    },
    async fetchHighlightData() {
      if (!this.selectedLiveId) return;
      try {
        const response = await axiosInstance.get(`/chzzk/hightlights/summary/${this.$route.params.channelId}`, {
          params: {
            live_key: this.selectedLiveId,
            period: this.period
          }
        });
        if (response.data.status_code === 200) {
          this.highlightData = response.data.content.highlights;
          this.selectedHighlightIndex = (this.highlightData && this.highlightData.length > 0) ? 0 : null;
          this.updateGraph();
        } else {
          console.error('하이라이트 데이터 로딩 실패:', response.data.detail);
        }
      } catch (error) {
        console.error('하이라이트 데이터 로딩 실패:', error);
      }
    },
    toggleHighlight(index) {
      this.selectedHighlightIndex = this.selectedHighlightIndex === index ? null : index;
    },
    updateGraph() {
      if (!this.highlightData || !this.highlightData.length) {
        if (this.chart) {
          this.chart.clear();
        }
        return;
      }

      if (!this.chart) {
        this.chart = echarts.init(this.$refs.graphContainer);
      }

      const data = this.highlightData.map(item => {
        let dateStr = item._id;
        if (/^\d{4}-\d{2}-\d{2} \d{2}$/.test(dateStr)) {
          dateStr = dateStr.replace(' ', 'T') + ':00:00';
        } else if (/^\d{4}-\d{2}-\d{2} \d{2}:\d{2}$/.test(dateStr)) {
          dateStr = dateStr.replace(' ', 'T') + ':00';
        } else {
          dateStr = dateStr.replace(' ', 'T');
        }
        return {
          value: [new Date(dateStr), item.count],
          itemStyle: { color: '#4CAF50' },
          time_unit: item._id,
          highlight_occurrences: item.count,
          avg_upper_band: item.avg_upper_band,
          avg_chat_count: item.avg_chat_count
        };
      });

      const option = {
        backgroundColor: '#1a1a1d',
        title: {
          text: '시간대별 하이라이트 발생 빈도',
          textStyle: {
            color: '#ffffff'
          }
        },
        tooltip: {
          trigger: 'axis',
          backgroundColor: 'rgba(50, 50, 50, 0.9)',
          borderColor: '#555',
          textStyle: {
            color: '#fff'
          }
        },
        grid: {
          left: 40,
          right: '4%',
          bottom: '3%', 
          containLabel: true
        },
        xAxis: {
          type: 'time',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#555'
            }
          },
          axisLabel: {
            color: '#fff',
            formatter: function (value) {
              const date = new Date(value);
              return (
                date.getHours().toString().padStart(2, '0') + ':' +
                date.getMinutes().toString().padStart(2, '0')
              );
            }
          }
        },
        yAxis: {
          type: 'value',
          name: '하이라이트 발생 횟수',
          nameTextStyle: {
            color: '#ccc'
          },
          axisLine: {
            lineStyle: {
              color: '#555'
            }
          },
          axisLabel: {
            color: '#fff'
          },
          splitLine: {
            lineStyle: {
              color: '#555'
            }
          }
        },
        series: [{
          name: '하이라이트',
          type: 'line',
          smooth: true,
          data: data.map(item => item.value),
          itemStyle: {
            color: '#4CAF50'
          },
          areaStyle: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: 'rgba(76, 175, 80, 0.3)'
            }, {
              offset: 1,
              color: 'rgba(76, 175, 80, 0.1)'
            }])
          },
          showSymbol: true,
          symbolSize: 10
        }]
      };

      this.chart.setOption(option);

      // 클릭 이벤트 리스너 등록 (중복 방지 위해 먼저 오프)
      this.chart.off('click');
      this.chart.on('click', (params) => {
        if (params.seriesType === 'line' && params.dataIndex != null) {
          this.selectedHighlightIndex = params.dataIndex;
          this.$nextTick(() => {
            const blocks = this.$el.querySelectorAll('.highlight-block');
            const container = this.$el.querySelector('.highlight-blocks');
            const block = blocks[params.dataIndex];
            if (block && container) {
              const blockTop = block.offsetTop;
              const blockHeight = block.offsetHeight;
              const containerHeight = container.offsetHeight;
              container.scrollTo({
                top: blockTop - containerHeight / 2 + blockHeight / 2,
                behavior: 'smooth'
              });
            }
          });
        }
      });
    },
    formatTime(timestamp) {
      let dateStr = timestamp;
      if (/^\d{4}-\d{2}-\d{2} \d{2}$/.test(dateStr)) {
        dateStr = dateStr.replace(' ', 'T') + ':00:00';
      } else if (/^\d{4}-\d{2}-\d{2} \d{2}:\d{2}$/.test(dateStr)) {
        dateStr = dateStr.replace(' ', 'T') + ':00';
      } else {
        dateStr = dateStr.replace(' ', 'T');
      }
      const date = new Date(dateStr);
      return date.toLocaleString('ko-KR', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      });
    }
  },
  mounted() {
    // this.fetchHighlightData();
    window.addEventListener('resize', () => {
      if (this.chart) {
        this.chart.resize();
      }
    });
  },
  beforeUnmount() {
    if (this.chart) {
      this.chart.dispose();
    }
    window.removeEventListener('resize', this.updateGraph);
  },
  watch: {
    period() {
      if (this.selectedLiveId) {
        this.fetchHighlightData();
      }
    }
  }
}
</script>

