<template>
    <ul class="navigation">
        <li v-for="(item, index) in categories" :key='index'>
            <router-link 
                :to="getCategoryLink(item.id)"
                :class="{'selectTag': isSelected(item.id)}"
                active-class=""
                exact-active-class=""
            >{{item.name}}</router-link>
        </li>
    </ul>
</template>
<script>
export default {
    name:"categoryNav",
    props: {
        currentCategory: {
            type: String,
            default: 'home'
        }
    },
    data(){
        return{
            categories:[
                { id: 'home', name: '홈' },
                { id: 'chat', name: '채팅 분석' },
                { id: 'highlight', name: '하이라이트 분석' },
            ]
        }
    },
    methods: {
        getCategoryLink(categoryId) {
            if (categoryId === 'home') {
                return `/studio/${this.$route.params.channelId}`;
            }
            return `/studio/${this.$route.params.channelId}/${categoryId}`;
        },
        isSelected(categoryId) {
            if (categoryId === 'home') {
                return this.$route.name === 'studio-default';
            }
            return this.$route.name === `studio-${categoryId}`;
        }
    }
}
</script>
<style scoped>
.navigation {
    list-style: none;
    display: flex;
    border-bottom: 1px solid #505050;
    padding: 20px 0 10px 0;
    margin: 0;
}

.navigation > li {
    font-size: 20px;
}

.navigation > li > a {
    color: #505050;
    text-decoration: none;
    cursor: pointer;
}

.navigation > li:nth-child(n+2):nth-child(-n+5) {
    margin-left: 40px;
}

.selectTag {
    color: white !important;
}
</style>