<template>
  <div class="findpassword-page">
    <div class="page-container">
      <div class="container">
        <h1>비밀번호 찾기</h1>
        <div class="subtitle">
          <p>비밀번호를 잊으셨나요?</p>
          <p>이메일 인증을 통해 새로운 임시 비밀번호 발급이 가능합니다.</p>
        </div>
        
        <form id="passwordRecoveryForm" @submit.prevent="sendVerificationEmail">
          <div class="input-section">
            <div class="input-group">
              <input 
                type="email" 
                id="email" 
                v-model="email" 
                placeholder="아이디(e-mail)를 입력해주세요." 
                required
                :class="{ error: !isEmailValid && email !== '', valid: isEmailValid && email !== '' }"
              >
            </div>
          </div>
          
          <button type="submit" :disabled="!isEmailValid">이메일 인증하기</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axiosInstance from '@/axios_config/axiosConfig';

export default {
  data() {
    return {
      email: '',
    };
  },
  
  computed: {
    isEmailValid() {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(this.email);
    }
  },
  
  methods: {
    async sendVerificationEmail() {
      if (this.isEmailValid) {
        if (confirm('인증 메일을 발송 하시겠습니까?')) {
          try {
            const response = await axiosInstance.post('/user/password-reset', {
              email: this.email
            });

            if (response.data.status_code === 200) {
              alert('비밀번호가 초기화되었습니다. 이메일을 확인해주세요.');
              this.email = '';
              this.$router.push('/login');
            } else {
              alert('비밀번호 초기화에 실패했습니다.');
            }
          } catch (error) {
            console.error('비밀번호 초기화 에러:', error);
            alert('비밀번호 초기화에 실패했습니다.');
          }
        }
      }
    }
  }
};
</script>

