  max-height: 500px; /* 사용자가 수정한 값 유지 */
  overflow-y: auto;
  padding-right: 5px;
}

/* 스크롤바 디자인 (선택적 - Webkit 기반 브라우저용) */
.chatter-list::-webkit-scrollbar {
  width: 8px;
}

.chatter-list::-webkit-scrollbar-track {
  background: #2c2c2e;
  border-radius: 4px;
}

.chatter-list::-webkit-scrollbar-thumb {
  background-color: #555;
  border-radius: 4px;
  border: 2px solid #2c2c2e;
}

.chatter-list::-webkit-scrollbar-thumb:hover {
  background-color: #777;
}

/* 플레이스홀더 메시지 스타일 */
.top-chatters-placeholder {
  /* 
    .top-chatters 클래스가 common.css에서 height: 100%를 가집니다.
    플레이스홀더도 유사한 공간을 차지하도록 height: 100%를 설정하고 내부에서 텍스트를 중앙 정렬합니다.
    .analysis-section (부모)가 display:flex, flex-direction:column 이므로,
    이 placeholder는 헤더를 제외한 나머지 공간을 채우려고 시도할 것입니다 (부모의 height가 고정되어 있으므로).
  */
  height: 100%; 
  display: flex;
  justify-content: center;
  align-items: center;
  color: #777; /* 적절한 색상으로 변경 가능 */
  font-size: 2rem; /* 적절한 크기로 변경 가능 */
  text-align: center;
}
</style> 