import {createStore} from 'vuex'
import axiosInstance from '@/axios_config/axiosConfig'
import axios from 'axios'

export default createStore({
    state : {
        user: null,
        isAuthenticated: false,
        isConnectChzzk: false,
    },
    mutations: {
        setUser(state, user) {
            state.user = user;
            state.isAuthenticated = !!user;
            state.isConnectChzzk = user?.isConnectChzzk || false;
        },
        clearUser(state) {
            state.user = null;
            state.isAuthenticated = false;
            state.isConnectChzzk = false;
        }
    },
    actions:{
        async checkSession({commit}) {
            try {
                const response = await axiosInstance.get('/user/getUserStatus', {
                    
                });
                
                if (response.data.status_code === 200) {
                    commit('setUser', response.data.content);
                    return true;
                } else {
                    commit('clearUser');
                    return false;
                }
            } catch (error) {
                commit('clearUser');
                return false;
            }
        },
        login({commit}, user){
            commit('setUser', user);
        },
        logout({commit}){
            commit('clearUser');
        },
        async refreshToken() {
            try {
                const response = await axios.post('https://www.h0bin2.store/user/token', {}, {
                    withCredentials: true
                });
                return response.status === 200;
            } catch (error) {
                console.error('토큰 갱신 실패:', error);
                return false;
            }
        }
    },

    getters: {
        isAuthenticated: state => state.isAuthenticated,
        currentUser: state => state.user,
        isConnectChzzk: state => state.isConnectChzzk
    }
})