<template>
    <input
      type="text"
      name="content"
      id=""
      placeholder="스트리머 검색"
      :value="modelValue"
      @input="onInput"
      @focus="$emit('focus')"
      @blur="$emit('blur')"
      autocomplete="off"
    >
</template>
<script>
export default {
    name:'inputText',
    props: {
        modelValue: String
    },
    methods: {
        onInput(e) {
            this.$emit('update:modelValue', e.target.value);
            this.$emit('input', e.target.value);
        }
    }
}
</script>
<style scoped>
input {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    background-color: #141517;
    color: white;
    border: 0px;
    width: 90%;
    height: 80%;
    font-size: 15px;
    border-radius: 6px;
    padding: 8px 12px;
    box-sizing: border-box;
}
</style>