<template>
    <div class="analysis-section">
        <div class="section-header">
            <h2>감정 분석</h2>
            <div class="filter-group">
                <div class="date-picker">
                    <input type="date" v-model="selectedSentimentDate" @change="fetchSentimentData">
                </div>
                <div class="date-filter">
                    <select v-model="selectedSentimentPeriod" @change="fetchSentimentData">
                        <option value="hour">시</option>
                        <option value="day">일</option>
                        <option value="month">월</option>
                        <option value="year">연</option>
                        <option value="all">전체</option>
                    </select>
                </div>
            </div>
        </div>
        <div class="emotion-trend-chart">
            <canvas id="sentimentPieChart"></canvas>
        </div>
    </div>
</template>

<script>
import Chart from 'chart.js/auto';
import axiosInstance from '@/axios_config/axiosConfig';
export default {
    name: 'SentimentAnalysis',
    props: {
        channelId: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            chatsentimentData: [
                {label: '행복함', value: '123'},
                {label: '화남', value: '123'},
                {label: '싫음', value: '23'},
                {label: '두려움', value: '163'},
                {label: '보통', value: '234'},
                {label: '슬픔', value: '112'},
                {label: '놀람', value: '143'},
            ],
            selectedSentimentPeriod: 'hour',
            selectedSentimentDate: new Date().toISOString().split('T')[0],
        }
    },
    mounted() {
        this.fetchSentimentData();
    },
    methods: {
        async fetchSentimentData() {
            try {
                const response = await axiosInstance.get(`/chzzk/${this.channelId}/sentiment-analysis`, {
                    params: {
                        period: this.selectedSentimentPeriod,
                        date: this.selectedSentimentDate
                    }
                });
                this.chatsentimentData = response.data.content.sentimentData;
                this.drawSentimentChart();
            } catch (error) {
                console.error('감정 분석 데이터 로딩 실패:', error);
            }
        },
        drawSentimentChart() {
            // 기존 차트 제거
            const existingChart = Chart.getChart('sentimentPieChart');
            if (existingChart) existingChart.destroy();

            // 데이터 정렬 (값이 큰 순서대로)
            const sortedData = [...this.chatsentimentData].sort((a, b) => b.value - a.value);

            const ctx = document.getElementById('sentimentPieChart').getContext('2d');
            new Chart(ctx, {
                type: 'doughnut',
                data: {
                    labels: sortedData.map(item => item.label),
                    datasets: [{
                        data: sortedData.map(item => item.value),
                        backgroundColor: [
                            '#4caf50',  // 행복함 - 초록색
                            '#e74c3c',  // 화남 - 빨간색
                            '#9c27b0',  // 싫음 - 보라색
                            '#ff9800',  // 두려움 - 주황색
                            '#9e9e9e',  // 보통 - 회색
                            '#2196f3',  // 슬픔 - 파란색
                            '#ffeb3b'   // 놀람 - 노란색
                        ],
                    }]
                },
                options: {
                    cutout: '70%',
                    plugins: { 
                        legend: { 
                            display: true, 
                            position: 'bottom',
                            labels: {
                                color: '#bbb',
                                padding: 20,
                                font: {
                                    size: 14,
                                    weight: '500'
                                }
                            }
                        },
                        tooltip: {
                            callbacks: {
                                label: function(context) {
                                    const value = context.raw;
                                    const total = context.dataset.data.reduce((a, b) => a + b, 0);
                                    const percentage = ((value / total) * 100).toFixed(1);
                                    return `${context.label}: ${value}개 (${percentage}%)`;
                                }
                            }
                        }
                    }
                }
            });
        }
    }
}
</script>

<style scoped>
@import '@/assets/chat-analysis-common.css';
</style> 