<template>
    <div class="routeHome">
        <mainBanner></mainBanner>
        <mainCategory></mainCategory>
        <liveGridLayout :globalChzzkLiveList='globalChzzkLiveList'></liveGridLayout>
    </div>
</template>
<script>
import axiosInstance from '@/axios_config/axiosConfig';
import liveGridLayout from '@/components/layout/liveGridLayout.vue';
import mainBanner from '@/components/layout/mainBanner.vue';
import mainCategory from '@/components/navgation/mainCategory.vue';


export default {
    name:'routeHome',
    components:{
        liveGridLayout,
        mainBanner,
        mainCategory
    },
    data(){
        return {
            globalChzzkLiveList: []
        }
    },
    computed: {

    },
    mounted(){
        this.loadPopularList();
    },
    methods:{
        async loadPopularList() {
            try {
                const response = await axiosInstance.get('/chzzk/highlight-channels', {
                    withCredentials: true,
                    headers: {
                        'Content-Type': 'application/json',
                        'Accept': 'application/json'
                    }
                });

                if (response.data.status_code === 200) {
                    this.globalChzzkLiveList = response.data.content.channels;
                } else {
                    console.error('Failed to load popular list');
                }
            } catch (error) {
                console.error('Error loading popular list:', error);
            }
        }
    }
}
</script>
<style scoped>
.routeHome {
    padding-top: 70px;
    min-height: calc(100vh - 50px);
    width: 100%;
}
</style>