<script>
import selectLive from "@/common/selectLive.vue";
import axiosInstance from "@/axios_config/axiosConfig";

export default {
  name: "NormalAnalysis",
  components: {
    selectLive,
  },
  props: {
    liveHistory: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      selectedLiveId: null,
      preLiveKey: null,
      channelChatSummary: null,
      selectedIdx: null,
      selectedYear: "2025",
      activeViewMode: "방송시간",
      highlightTimeline: null,
      highlightHeatmap: {},
    };
  },
  watch: {
    liveHistory: {
      immediate: true,
      handler(newVal) {
        if (newVal && newVal.length > 0) {
          this.selectedLiveId = newVal[0].live_key;
          this.handleSelectedLive(this.selectedLiveId);
        }
      },
    },
  },
  computed: {
    yearList() {
      // liveHistory에서 연도 추출 (중복 제거)
      const years = this.liveHistory.map((live) =>
        new Date(live.session_start_time).getFullYear()
      );
      return [...new Set(years)].sort((a, b) => b - a);
    },
    activityMap() {
      const map = {};
      this.liveHistory.forEach((live) => {
        const start = new Date(live.session_start_time);
        const end = new Date(live.session_end_time);
        // 선택된 연도에 해당하는 데이터만 집계
        if (
          start.getFullYear() > Number(this.selectedYear) ||
          end.getFullYear() < Number(this.selectedYear)
        ) {
          return;
        }
        let cur = new Date(start);
        while (cur <= end) {
          const dateStr = cur.toISOString().slice(0, 10);
          if (cur.getFullYear() !== Number(this.selectedYear)) {
            cur.setDate(cur.getDate() + 1);
            cur.setHours(0, 0, 0, 0);
            continue;
          }
          let dayStart = new Date(cur);
          dayStart.setHours(0, 0, 0, 0);
          let dayEnd = new Date(cur);
          dayEnd.setHours(23, 59, 59, 999);
          const sessionStart = cur > start ? cur : start;
          const sessionEnd = dayEnd < end ? dayEnd : end;
          const hours = (sessionEnd - sessionStart) / (1000 * 60 * 60);
          map[dateStr] = (map[dateStr] || 0) + hours;
          cur.setDate(cur.getDate() + 1);
          cur.setHours(0, 0, 0, 0);
        }
      });
      return map;
    },
  },
  methods: {
    async loadHighlightTimeline() {
      try {
        const response = await axiosInstance.get(
          `/chzzk/${this.$route.params.channelId}/highlights/${this.selectedDate}`,
          {}
        );
        console.log(response.data);
        if (response.data.status_code === 200) {
          this.highlightTimeline = response.data.content.highlights;
        } else {
          alert("하이라이트 타임라인을 불러오지 못했습니다.");
          location.href = "/";
        }
      } catch (error) {
        console.error("Error loading highlight timeline:", error);
        alert("하이라이트 타임라인을 불러오지 못했습니다.");
        location.href = "/";
      }
    },
    async loadHighlightHeatmap() {
      try {
        const response = await axiosInstance.get(
          `/chat/analytics/highlight_heatmap`,
          {
            params: {
              channel_id: this.$route.params.channelId,
            },
          }
        );
        if (response.data.status_code === 200) {
          this.highlightHeatmap = response.data.content.heatmap;
          console.log(this.highlightHeatmap);
        } else {
          alert("하이라이트 히트맵을 불러오지 못했습니다.");
          this.activeViewMode = "방송시간";
        }
      } catch (error) {
        console.error("하이라이트 히트맵 로딩 실패:", error);
        this.highlightHeatmap = {};
      }
    },
    handleSelectedLive(selectedId) {
      this.selectedLiveId = selectedId;
      const selectLiveIndex = this.liveHistory.findIndex(
        (live) => live.live_key === this.selectedLiveId
      );
      const preLiveIndex = this.liveHistory[selectLiveIndex + 1];
      this.preLiveKey = preLiveIndex ? preLiveIndex.live_key : null;
      if (this.selectedLiveId) {
        this.getChannelChatSummary();
      }
    },
    async getChannelChatSummary() {
      const response = await axiosInstance.get(
        `/chat/analytics/channel_chat_summary`,
        {
          params: {
            channel_id: this.$route.params.channelId,
            live_key: this.selectedLiveId,
            pre_live_key: this.preLiveKey,
          },
        }
      );
      if (response.data.status_code === 200) {
        this.channelChatSummary = response.data.content.channel_summary;
        this.selectedIdx = this.liveHistory.findIndex(
          (live) => live.live_key === this.selectedLiveId
        );
      } else {
        alert("라이브 데이터를 불러오지 못 했습니다.");
      }
    },
    getDateFromWeekDay(week, day) {
      const year = this.selectedYear;
      const firstDay = new Date(`${year}-01-01`);
      const firstDayOfWeek = firstDay.getDay();
      const daysToAdd = (week - 1) * 7 + (day - 1) - firstDayOfWeek;
      const date = new Date(firstDay);
      date.setDate(firstDay.getDate() + daysToAdd);
      // 유효성 체크
      if (isNaN(date.getTime())) return null;
      // 연도 범위 벗어나면 null
      if (date.getFullYear() !== Number(year)) return null;
      return date.toISOString().slice(0, 10);
    },
    getActivityLevel(week, day) {
      const dateStr = this.getDateFromWeekDay(week, day);
      if (!dateStr) return "none";
      const hours = this.activityMap[dateStr] || 0;
      if (hours === 0) return "none";
      if (hours < 2) return "low";
      if (hours < 4) return "medium";
      if (hours < 8) return "high";
      if (hours < 10) return "very-high";
      return "very-very-high";
    },
    isMonthStartWeek(week) {
      const year = this.selectedYear;
      for (let month = 2; month <= 12; month++) {
        const firstDay = new Date(
          `${year}-${String(month).padStart(2, "0")}-01`
        );
        const firstWeek = this.getWeekNumber(firstDay);
        if (week === firstWeek) return true;
      }
      return false;
    },
    getWeekNumber(date) {
      const d = new Date(
        Date.UTC(date.getFullYear(), date.getMonth(), date.getDate())
      );
      const dayNum = d.getUTCDay() || 7;
      d.setUTCDate(d.getUTCDate() + 4 - dayNum);
      const yearStart = new Date(Date.UTC(d.getUTCFullYear(), 0, 1));
      return Math.ceil(((d - yearStart) / 86400000 + 1) / 7);
    },
    getDaysInMonth(year, month) {
      // month: 1~12
      return new Date(year, month, 0).getDate();
    },
    getActivityLevelByDate(year, month, day) {
      const dateStr =
        year +
        "-" +
        String(month).padStart(2, "0") +
        "-" +
        String(day).padStart(2, "0");
      const hours = this.activityMap[dateStr] || 0;
      if (hours === 0) return "none";
      if (hours < 2) return "low";
      if (hours < 4) return "medium";
      if (hours < 8) return "high";
      if (hours < 10) return "very-high";
      return "very-very-high";
    },
    getMonthColCount(year, month) {
      const days = this.getDaysInMonth(year, month);
      return Math.ceil(days / 7);
    },
    getDayByColRow(year, month, col, row) {
      // col: 1~N, row: 1~7
      const days = this.getDaysInMonth(year, month);
      const day = (col - 1) * 7 + row;
      return day > 0 && day <= days ? day : null;
    },
    // 최근 한 달 기준 highlightHeatmap 최대값으로 6등분 경계값 계산
    getHighlightThresholds() {
      // 오늘 날짜 기준 최근 30일
      const today = new Date();
      const monthAgo = new Date();
      monthAgo.setDate(today.getDate() - 30);
      // liveHistory에서 최근 30일 내 세션의 live_key만 추출
      const recentLiveKeys = this.liveHistory
        .filter(l => {
          const start = new Date(l.session_start_time);
          return start >= monthAgo && start <= today;
        })
        .map(l => l.live_key);
      // 해당 live_key의 count만 추출
      const counts = recentLiveKeys.map(key => this.highlightHeatmap[key] || 0);
      const max = Math.max(...counts, 0);
      // 0~max를 6등분
      const step = max / 6;
      // 경계값 배열 반환 (0, step, 2*step, ...)
      const thresholds = [0, step, 2*step, 3*step, 4*step, 5*step, max+1];
      console.log(thresholds);
      return thresholds;
    },
    getHighlightLevelByDate(year, month, day) {
      if (!day) {
        return "none";
      }
      // const dateStr = year + "-" + String(month).padStart(2, "0") + "-" + String(day).padStart(2, "0");
      const live = this.liveHistory.find(l => {
        const start = new Date(l.session_start_time);
        return start.getFullYear() === Number(year) &&
               start.getMonth() + 1 === Number(month) &&
               start.getDate() === Number(day);
      });
      if (!live) return "none";
      const count = this.highlightHeatmap[live.live_key] || 0;
      const thresholds = this.getHighlightThresholds();
      if (count === 0) return "none";
      if (count < thresholds[1]) return "low";
      if (count < thresholds[2]) return "medium";
      if (count < thresholds[3]) return "high";
      if (count < thresholds[4]) return "very-high";
      if (count < thresholds[5]) return "very-very-high";
      return "very-very-high";
    },
  },
};
</script>

