<template>
    <div class="setting-layout">
        <div class="left-container">
            <div class="header-container">
                <a href="/" class="header-title">
                    THOTH
                </a>
            </div>
            <div class="profile-container">
                <aImgTag href="#" :isOnline="false" :size="'150'" :filename="'anonymous.png'"></aImgTag>
