                    {{ user.username }}
                </div>
            </div>
            <div class="chzzk-container">
                
            </div>
        </div>
        <div class="right-container">
            <div class="setting-section">
                <div class="setting-header">
                    <h2 class="setting-title">계정 정보</h2>
                </div>
                <div class="setting-item">
                    <span class="item-label">로그인 상태</span>
                    <div class="item-value">
                        <span class="status-text" :class="{ 'status-active': isAuthenticated }">
                            {{ isAuthenticated ? '로그인됨' : '로그인 필요' }}
                        </span>
                    </div>
                </div>
                <div class="setting-item">
                    <span class="item-label">비밀번호 변경</span>
                    <div class="item-value">
                        <button class="edit-button" @click="handlePasswordChange">변경</button>
                    </div>
                </div>
            </div>

            <div class="setting-section">
                <div class="setting-header">
                    <h2 class="setting-title">계정 연동</h2>
                </div>
