                    <span class="item-label">치지직 계정 연동</span>
                    <div class="item-value">
                        <span class="status-text" :class="{ 'status-active': isConnectChzzk }">
                            {{ isConnectChzzk ? '연동됨' : '미연동' }}
                        </span>
                        <button v-if="!isConnectChzzk" class="edit-button" @click="handleChzzkLink">
                            연동하기
                        </button>
                    </div>
                </div>
            </div>

            <div class="setting-section">
                <div class="setting-header">
                    <h2 class="setting-title">기능 설정</h2>
                </div>
                <div class="setting-item">
                    <span class="item-label">채팅 추적</span>
                    <div class="item-value">
                        <span class="status-text" :class="{ 'status-active': isTracking }">
                            {{ isTracking ? '활성화' : '비활성화' }}
                        </span>
                        <label class="toggle-switch">
                            <input 
                                type="checkbox" 
                                :checked="isTracking" 
                                @change="handleTrackingOnOff"
                            >
                            <span class="toggle-slider"></span>
                        </label>
                    </div>
                </div>
                <div class="setting-item">
                    <span class="item-label">에이전트 구독</span>
                    <div class="item-value">
                        <span class="status-text" :class="{ 'status-active': isAgentSubscribed }">
                            {{ isAgentSubscribed ? '구독 중' : '미구독' }}
                        </span>
                        <label class="toggle-switch">
                            <input 
                                type="checkbox" 
                                :checked="isAgentSubscribed" 
                                @change="handleAgentSubscription"
                            >
                            <span class="toggle-slider"></span>
                        </label>
                    </div>
                </div>
            </div>

