                    </div>
                </div>
                <div class="setting-item">
                    <span class="item-label">회원 탈퇴</span>
                    <div class="item-value">
                        <button class="danger-button" @click="handleAccountDeletion">탈퇴하기</button>
                    </div>
                </div>
            </div>

