<script>
import axiosInstance from '@/axios_config/axiosConfig';
import aImgTag from '@/common/aImgTag.vue'
import { mapState, mapGetters } from 'vuex';

export default {
    name:'settingLayout',
    components:{
        aImgTag
    },
    data(){
        return {
        }
    },
    computed: {
        ...mapState({
            user: state => state.user,
            isTracking: state => state.user.isTracking,
            isAgentSubscribed: state => state.user.isAgentSub
        }),
        ...mapGetters(['isAuthenticated', 'isConnectChzzk'])
    },
    mounted(){
        this.isLoggedIn = !!this.$store.state.user;
    },
    methods: {
        async handleLogout() {
            try {
                const response = await axiosInstance.post('/user/logout');
                if (response.data.status_code === 200) {
                    console.log('로그아웃 성공');
                    this.$store.dispatch('logout');
                    this.$router.push('/');
                } else {
                    console.error('로그아웃 실패');
                }
            } catch (error) {
                console.error('로그아웃 실패:', error);
            }
        },
        async handleTrackingOnOff() {
            try {
                const response = await axiosInstance.put('/user/chatTrackingAgree', 
                    {},
                );

                if (response.data.status_code === 200) {
                    await this.$store.dispatch('checkSession');
                } else {
                    console.error('탐지 상태 변경 실패');
                }
            } catch (error) {
                console.error('에러 발생:', error);
            }
        },
        async handlePasswordChange() {
            //비밀번호 변경 로직
            console.log('비밀번호 변경 요청')
            this.$router.push('/passwordchange');
        },
        async handleChzzkLink() {
            try {
                // 백엔드에서 세션 생성 및 연동 URL 요청
                const response = await axiosInstance.get('/user/chzzk-auth/init');
                console.log('치지직 연동 응답:', response.data);  // 디버깅용 로그
                
                if (response.data.status_code === 200) {
                    // 받은 URL로 리다이렉트
                    window.location.href = response.data.content.auth_url;
                    // this.$router.push(response.data.content.auth_url);
                } else {
                    console.error('치지직 연동 URL 생성 실패:', response.data.detail);
                }
            } catch (error) {
                console.error('치지직 연동 초기화 실패:', error);
            }
        },
        async handleAgentSubscription() {
            try {
                const response = await axiosInstance.put('/user/agentSubAgree', 
                    {},
                    {

                    }
                );
                if (response.data.status_code === 200) {
                    await this.$store.dispatch('checkSession');
                } else {
                    console.error('구독 상태 변경 실패');
                }
            } catch (error) {
                console.log('에러 발생 : ', error);
            }
        },
        async handleAccountDeletion() {
            // 회원 탈퇴 로직
            if (confirm('정말로 탈퇴하시겠습니까? 이 작업은 되돌릴 수 없습니다.')) {
                console.log('회원 탈퇴 요청')
                try {
                    const response = await axiosInstance.post('/user/expire', {
                    
                    });

                    if (response.data.status_code === 200) {
                        alert('회원 탈퇴가 완료되었습니다.');
                        this.$store.dispatch('logout');
                        this.$router.push('/');
                    } else {
                        alert('회원 탈퇴에 실패했습니다.');
                    }
                } catch (error) {
                    console.error('회원 탈퇴 에러:', error);
                    alert('회원 탈퇴에 실패했습니다.');
                }
            }
        },
    }
}
</script>
