<template>
    <div class="studio-layout">
        <div class="studio-header">
            <div class="channel-profile">
                <div class="image-container">
                    <aImgTag class="channel-image" :href="''" :size="'110'" :filename="channelImageUrl" :isOnline="true"></aImgTag>
                </div>
                <div class="channel-info">
                    <subTitle class="channelName" :title="channelInfo['channelName']" fontWeight="40"></subTitle>
                    <a class='chzzk-link' :href="'https://chzzk.naver.com/' + channelInfo['channelId']" target="_blank">플랫폼 이동 ></a>
                </div>
            </div>
            <div class="channel-category">
                <categoryNav :currentCategory="$route.name"></categoryNav>
            </div>
        </div>
        <div class="channel-analysis">
            <div v-if="$route.name === 'studio-highlight'" class="analysis-params">
            </div>
            <div class="analysis-content">
                <router-view :liveHistory="liveHistory"></router-view>
            </div>
        </div>
    </div>
</template>
<script>
import aImgTag from '@/common/aImgTag.vue'
import subTitle from '@/common/subTitle.vue'
import categoryNav from '@/common/categoryNav.vue';
import { mapState } from 'vuex'
import axiosInstance from '@/axios_config/axiosConfig';
export default {
    name:'studioLayout',
    components:{
        aImgTag,
        subTitle,
        categoryNav
    },
    data(){
        return {
            channelInfo:{},
            channelImageUrl:'',
            selectedDate:new Date().toISOString().split('T')[0],
            liveHistory:[]
        }
    },
    computed: {
        ...mapState(['isAuthenticated', 'isConnectChzzk'])
    },
    methods:{
        async loadChannelinfo(){
            try {
                const response = await axiosInstance.get(`/chzzk/channels/${this.$route.params.channelId}`, {
                });

                if(response.data.status_code === 400){
                    alert('정보를 불러오지 못했습니다.');
                    this.$router.push('/');
                }

                this.channelInfo = response.data.content;

                //만약 이미지가 None이라면 기본 이미지로 설정
                if (this.channelInfo.channelImageUrl) {
                    this.channelImageUrl = this.channelInfo.channelImageUrl;
                } else {
                    this.channelImageUrl = 'anonymous.png';
                }
            }catch (error) {
                // console.error('Error loading channel info:', error);
                alert('정보를 불러오지 못했습니다.');
                this.$router.push('/');
            }
        },
        async getLiveHistory(){
            try {
                const response = await axiosInstance.get(
                    `/chat/analytics/live_history`,
                    {
                        params:{
                            channel_id:this.$route.params.channelId
                        },
                    }
            );
                this.liveHistory = response.data.content.session_history;
                if(this.liveHistory.length === 0){
                    alert('채팅 추적을 허용하지 않은 사용자이거나, 라이브 이력이 없습니다.');
                }
                // console.log(this.liveHistory);
            }catch (error) {
                // console.error('Error loading live history:', error);
                alert('라이브 이력을 불러오지 못했습니다.');
                this.$router.push('/');
            }
        }
    },
    created(){
        if(this.isConnectChzzk){
            this.getLiveHistory();
        }else{
            alert('채널 연동이 필요합니다.');
            this.$router.push('/settings');
        }
    },
    mounted() {
        this.loadChannelinfo();
    },
    watch: {
        '$route.params.channelId'() {
            this.getLiveHistory();
            this.loadChannelinfo();
            this.loadHighlightTimeline();
        }
    }
}
</script>
