<template>
    <div class="analysis-section">
        <div class="wordcloud-box">
            <div class="wordcloud-header">
                <h2>워드 클라우드</h2>
                <div class="date-filter">
                    <SelectLive :liveList="liveHistory" @selected="handleSelectedLive" />
                </div>
            </div>
            <div class="wordcloud-placeholder">워드 클라우드 영역</div>
        </div>
    </div>
</template>

<script>
import SelectLive from '../../common/selectLive.vue';
import axiosInstance from '@/axios_config/axiosConfig';
export default {
    name: 'WordCloud',
    components: {
        SelectLive
    },
    props: {
        channelId: {
            type: String,
            required: true
        },
        liveHistory: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            wordcloudData: [],
            selectedWordcloudPeriod: 'hour',
            selectedWordcloudDate: new Date().toISOString().split('T')[0],
        }
    },
    mounted() {
        this.fetchWordcloudData();
    },
    methods: {
        handleSelectedLive(selectedId) {
            this.selectedWordcloudDate = selectedId;
            if (this.selectedWordcloudDate) {
                this.fetchWordcloudData();
            } else {
                this.wordcloudData = [];
            }
        },
        async fetchWordcloudData() {
            try {
                const response = await axiosInstance.get(`/chat/analytics/wordcloud`, {
                    params: {
                        channel_id: this.channelId,
                        live_key: this.selectedWordcloudDate,
                        period: this.selectedWordcloudPeriod,
                    }
                });
                if (response.status === 200) {
                    this.wordcloudData = response.data.content.wordcloud;
                    this.$nextTick(() => {
                        this.drawWordcloud();
                    });
                } else {
                    console.error('워드클라우드 데이터 로딩 실패:', response.data.detail);
                }
            } catch (error) {
                console.error('워드클라우드 데이터 로딩 실패:', error);
            }
        },
        drawWordcloud() {
            const container = document.querySelector('.wordcloud-placeholder');
            if (!container) return;
            
            container.innerHTML = '';
            container.style.height = '500px';

            const canvas = document.createElement('canvas');
            container.appendChild(canvas);

            canvas.width = container.offsetWidth;
            canvas.height = container.offsetHeight;
            const ctx = canvas.getContext('2d');

            ctx.fillStyle = '#1a1a1d';
            ctx.fillRect(0, 0, canvas.width, canvas.height);

            const sortedWords = [...this.wordcloudData].sort((a, b) => b.value - a.value);
            const maxValue = Math.max(...sortedWords.map(word => word.value));
            const colors = ['#4caf50', '#2196f3', '#ff9800', '#e74c3c', '#9c27b0', '#00bcd4', '#ffeb3b', '#795548'];
            const gridSize = 10;
            const grid = Array(Math.ceil(canvas.height / gridSize)).fill()
                .map(() => Array(Math.ceil(canvas.width / gridSize)).fill(false));
            function checkCollision(x, y, width, height) {
                const startX = Math.floor(x / gridSize);
                const startY = Math.floor(y / gridSize);
                const endX = Math.ceil((x + width) / gridSize);
                const endY = Math.ceil((y + height) / gridSize);
                for (let i = startY; i < endY; i++) {
                    for (let j = startX; j < endX; j++) {
                        if (i >= 0 && i < grid.length && j >= 0 && j < grid[0].length) {
                            if (grid[i][j]) return true;
                        }
                    }
                }
                return false;
            }
            function markGrid(x, y, width, height) {
                const startX = Math.floor(x / gridSize);
                const startY = Math.floor(y / gridSize);
                const endX = Math.ceil((x + width) / gridSize);
                const endY = Math.ceil((y + height) / gridSize);
                for (let i = startY; i < endY; i++) {
                    for (let j = startX; j < endX; j++) {
                        if (i >= 0 && i < grid.length && j >= 0 && j < grid[0].length) {
                            grid[i][j] = true;
                        }
                    }
                }
            }
            function* spiralPoints(centerX, centerY) {
                let angle = 0;
                let radius = 0;
                const step = 0.1;
                const radiusStep = 2;
                while (true) {
                    radius += step;
                    angle += step;
                    const x = centerX + radius * Math.cos(angle) * radiusStep;
                    const y = centerY + radius * Math.sin(angle) * radiusStep;
                    yield {x, y};
                    if (radius > Math.max(canvas.width, canvas.height)) break;
                }
            }
            sortedWords.forEach((word, index) => {
                const fontSize = Math.max(40, Math.min(120, (word.value / maxValue) * 110));
                ctx.font = `bold ${fontSize}px Arial`;
                ctx.fillStyle = colors[index % colors.length];
                const metrics = ctx.measureText(word.text);
                const width = metrics.width;
                const height = fontSize;
                const spiral = spiralPoints(canvas.width / 2, canvas.height / 2);
                let position = null;
                for (const point of spiral) {
                    const x = point.x - width / 2;
                    const y = point.y + height / 2;
                    if (x < 0 || x + width > canvas.width || y < 0 || y - height > canvas.height) {
                        continue;
                    }
                    if (!checkCollision(x, y - height, width, height)) {
                        position = {x, y};
                        markGrid(x, y - height, width, height);
                        break;
                    }
                }
                if (position) {
                    ctx.fillText(word.text, position.x, position.y);
                }
            });
        },
    }
}
</script>

<style scoped>
@import '@/assets/chat-analysis-common.css';
</style> 