<template>
  <div id="app">
    <topHeader 
      :isLoggedIn="isAuthenticated"
      :user="user"
      v-if="showNav"
    />
    <!-- <leftSideNav v-if="showNav" /> -->
    <router-view></router-view>
  </div>
</template>

<script>
import topHeader from '@/components/navgation/topHeader.vue';
// import leftSideNav from '@/components/navgation/leftSideNav.vue';
import {mapState, mapActions, mapGetters} from 'vuex';
import {useRoute} from 'vue-router'

export default {
  name: 'App',
  components: {
    topHeader,
    // leftSideNav
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isAuthenticated']),
    showNav() {
      return this.$route.name !== 'login' && this.$route.name !== 'settings' && this.$route.name !== 'signup' && this.$route.name !== 'findpassword' 
          && this.$route.name !== 'passwordchange' && this.$route.name !== 'HighlightAnalysis' && this.$route.name !== 'ChatAnalysis' 
          // && this.$route.name !== 'chatting' 
    }
  },
  data() {
    return {
      route: useRoute()
    }
  },
  methods: {
    ...mapActions(['checkSession'])
  }
}
</script>

<style>
* {
  font-family: 'tossFont', Helvetica, Arial, sans-serif;
}
