<template>
    <div class="login-container">
    <div class="login-form">
      <h2>로그인</h2>
      <div class="input-container">
        <input 
          id="email" 
          class="input-field" 
          type="email" 
          v-model="user.email"
          placeholder=" ">
        <label class="floating-label" for="email">이메일</label>
      </div>
      <div class="input-container password-container">
        <input 
          id="password" 
          class="input-field" 
          :type="isShowPassword ? 'text' : 'password'" 
          v-model="user.password"
          placeholder=" ">
        <label class="floating-label" for="password">비밀번호</label>
        <button type="button" class="password-hidetoggle" @click="showPasswordToggle">
          <img 
            :src="isShowPassword ? require('@/assets/open-eye.svg') : require('@/assets/close-eyes.svg')" 
            :alt="isShowPassword ? '비밀번호 숨기기' : '비밀번호 표시'"
          >
        </button>
      </div>
      <button class="login-button" @click="handleLogin">로그인</button>
      <div class="options">
        <label class="keep-log">
          <input type="checkbox" v-model="refresh_agree"> 로그인 유지
        </label>
        <div class="links">
          <router-link to="/findpassword">비밀번호 찾기</router-link>
          <router-link to="/signup">회원가입</router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapState } from 'vuex'
import axiosInstance from '@/axios_config/axiosConfig'

export default {
    name:'routeLogin',
    components:{
        
    },
    data(){
        return {
            user: {
                email: '',
                password: ''
            },
            isShowPassword: false,
            refresh_agree: false
        }
    },
    computed: {
        ...mapState(['isAuthenticated'])
    },
    methods:{
        ...mapActions(['login']),
        
        async handleLogin() {
            try {
                if (!this.user.email || !this.user.password) {
                    alert('아이디/비밀번호를 입력해주세요.');
                    return;
                }

                const response = await axiosInstance.post('/user/login', {
                    email: this.user.email,
                    password: this.user.password,
                    refresh_agree: this.refresh_agree
                });

                if (response.data.status_code === 200) {
                    await this.login(response.data.content.user_id_hash || {});
                    
                    const redirectPath = this.$route.query.redirect || '/';
                    this.$router.push(redirectPath);
                  } else {
                    alert('로그인에 실패했습니다.');
                }
            } catch (error) {
                console.error('Login error:', error);
                alert('로그인에 실패했습니다.');
            }
        },
        showPasswordToggle(){
            this.isShowPassword = !this.isShowPassword;
        }
    },
    watch: {
        isAuthenticated(newValue) {
            if (newValue) {
                this.$router.push('/');
            }
        }
    },
    mounted() {
        // 컴포넌트가 마운트될 때 로그인 상태 확인
        if (this.isAuthenticated) {
            this.$router.push('/');
        }
    }
}
</script>
