<template>
    <router-link :to="to" :class="{online:isOnline}" :style="{width: size + 'px', height: size + 'px'}">
        <img :src="imagePath" alt="">
    </router-link>
</template>

<script>
export default {
    name:'aImgTag',
    props:{
        to : {
            type:String
        },
        size:{
            type : String
        },
        filename : {
            type:String,
            required: true
        },
        isOnline: {
            type:Boolean
        }
    },
    computed:{
        imagePath(){
            try{
                return require(`@/assets/${this.filename}`);
            }catch {
                return this.filename;
            }
        }
    }
}
</script>
<style scoped>
img {
    width: 100%;
    height: 100%;
    border-radius: 40em;
}

a {
    display: block;
    padding: 5px 4px 5px 4px !important;
    border: 0px;
    border-radius: 40em;
}

.online {
    padding: 2px;
    border: 3px solid #8CF064;
}
</style>