<template>
    <div class="analysis-section">
        <div class="section-header">
            <h2>시청자 대비 채팅 참여자 비율</h2>
            <div class="filter-group">
                <div class="date-filter">
                    <SelectLive :liveList="liveHistory" @selected="handleSelectedLive" />
                    <select v-model="selectedRatioPeriod" @change="fetchRatioData">
                        <option value="hour">시</option>
                        <option value="day">일</option>
                        <option value="month">월</option>
                        <option value="year">연</option>
                        <option value="all">전체</option>
                    </select>
                </div>
            </div>
        </div>
        <div class="chat-ratio-charts">
            <!-- <div class="chat-ratio-chart">
                <h4>채팅수 추이</h4>
                <canvas id="chatCountChart"></canvas>
            </div> -->
            <div class="chat-ratio-chart">
                <div class="canvas-wrapper">
                    <canvas id="chatParticipationChart"></canvas>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Chart from 'chart.js/auto';
import SelectLive from '../../common/selectLive.vue';
import axiosInstance from '@/axios_config/axiosConfig';

export default {
    name: 'ChatRatio',
    components: {
        SelectLive
    },
    props: {
        channelId: {
            type: String,
            required: true
        },
        liveHistory: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            rawChatRatioData: [], // API 원본 데이터를 저장할 배열
            selectedRatioPeriod: 'hour',
            selectedLiveId: '',
        }
    },
    computed: {
        chatRatioData() {
            if (!this.rawChatRatioData || this.rawChatRatioData.length === 0) {
                return {
                    dates: [],
                    chatCounts: [],
                    viewers: [],
                    participation: [],
                    participationCount: [],
                    avgViewers: []
                };
            }
            return {
                dates: this.rawChatRatioData.map(r => (r._id ? r._id.split('_').pop() + '시' : '')),
                chatCounts: this.rawChatRatioData.map(r => r.chat_count ?? 0),
                viewers: [], // 필요시 API 응답에 맞게 수정
                participation: this.rawChatRatioData.map(r => r.participation_ratio ?? 0),
                participationCount: this.rawChatRatioData.map(r => r.participation_count ?? 0),
                avgViewers: this.rawChatRatioData.map(r => r.avg_viewers ?? 0),
            };
        }
    },
    mounted() {

    },
    methods: {
        handleSelectedLive(selectedId) {
            this.selectedLiveId = selectedId;
            if (this.selectedLiveId) {
                this.fetchRatioData();
            }
        },
        async fetchRatioData() {
            try {
                const response = await axiosInstance.get(`/chat/analytics/chat_ratio`, {
                    params: {
                        channel_id: this.channelId,
                        period: this.selectedRatioPeriod,
                        live_key: this.selectedLiveId
                    }
                });
                this.rawChatRatioData = response.data.content.chat_ratio || [];
                this.drawChatRatioCharts();
            } catch (error) {
                console.error('채팅 비율 데이터 로딩 실패:', error);
                this.rawChatRatioData = []; // 에러 시 초기화
            }
        },
        drawChatRatioCharts() {
            const existingParticipationChart = Chart.getChart('chatParticipationChart');
            if (existingParticipationChart) existingParticipationChart.destroy();
            
            const participationCtx = document.getElementById('chatParticipationChart').getContext('2d');
            const self = this;
            new Chart(participationCtx, {
                type: 'line',
                data: {
                    labels: this.chatRatioData.dates,
                    datasets: [{
                        label: '참여율',
                        data: this.chatRatioData.participation,
                        borderColor: '#8CF064',
                        backgroundColor: 'rgba(140, 240, 100, 0.1)',
                        tension: 0.4,
                        fill: true,
                        pointRadius: 4,
                        pointBackgroundColor: '#8CF064'
                    }]
                },
                options: {
                    responsive: true,
                    maintainAspectRatio: false,
                    plugins: {
                        legend: { display: false },
                        tooltip: {
                            callbacks: {
                                label: function(context) {
                                    const idx = context.dataIndex;
                                    const ratio = self.chatRatioData.participation[idx];
                                    const count = self.chatRatioData.participationCount[idx];
                                    const viewers = self.chatRatioData.avgViewers[idx];
                                    return [
                                        `참여율: ${ratio.toFixed(2)}%`,
                                        `채팅 참여자 수: ${count}명`,
                                        `평균 시청자 수: ${Math.round(viewers)}명`
                                    ];
                                }
                            }
                        }
                    },
                    scales: {
                        y: {
                            beginAtZero: true,
                            grid: { color: '#333' },
                            ticks: { 
                                color: '#bbb',
                                callback: function(value) {
                                    return value + '%';
                                }
                            }
                        },
                        x: {
                            grid: { color: '#333' },
                            ticks: { color: '#bbb' }
                        }
                    }
                }
            });
        }
    }
}
</script>

<style scoped>
@import '@/assets/chat-analysis-common.css';

.chat-ratio-chart {
  width: 100%;
  height: 400px;
  min-width: 300px;
  min-height: 200px;
}
.chat-ratio-chart canvas {
  width: 100% !important;
  height: 100% !important;
  display: block;
}
</style> 