<template>
    <div class="analysis-section">
        <div class="section-header">
            <h2>내 채널 요약</h2>
            <div class="filter-group">
                <div class="date-filter">
                    <div v-if="!summary">
                        <button class="custom-select summary-btn" @click="makeSummary">요약 생성</button>
                    </div>
                    <SelectLive :liveList="liveHistory"  @selected="handleSelectedLive" />
                </div>
            </div>
        </div>
        <div class="summary-content">
            <axiosSpinner v-if="isLoading" />
            <div v-if="summary" v-html="summary"></div>
            <div v-else>
                <p>요약 데이터가 없습니다.</p>
            </div>
        </div>
    </div>
</template>

<script>
import SelectLive from '@/common/selectLive.vue'
import axiosInstance from '@/axios_config/axiosConfig';
import axiosSpinner from '@/components/spinner/axiosSpinner.vue'
export default {
    name: 'ChannelSummary',
    components: {
        SelectLive,
        axiosSpinner
    },
    props: {
        liveHistory: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            selectedLiveId: null,
            summary: null,
            isLoading: false
        }
    },
    methods: {
        handleSelectedLive(selectedId) {
            this.selectedLiveId = selectedId;
            if (this.selectedLiveId) {
                this.getSummary();
            }
        },
        async getSummary() {
            const response = await axiosInstance.get(
                `/chat/analytics/getSummary`,
                {
                    params: {
                        channel_id: this.$route.params.channelId,
                        live_key: this.selectedLiveId
                    }
                }
            );
            if (response.data.status_code == 200) {
                const content = response.data.content.summary;
                if (content && typeof content.summary === 'string') {
                  this.summary = content.summary.replace(/\n/g, '<br>');
                } else {
                  this.summary = null;
                }
                console.log(this.summary);
            } else {
                this.summary = null;
                // alert(response.data.detail);
            }
        },
        async makeSummary() {
            if (!this.selectedLiveId) {
                alert('생성할 라이브를 선택해주세요');
                return;
            }
            this.isLoading = true;
            try {
                const response = await axiosInstance.post(
                    `/chat/analytics/makeSummary`,
                    {
                        channel_id: this.$route.params.channelId,
                        live_key: this.selectedLiveId
                    }
                );
                if (response.data.status_code == 200) {
                    const content = response.data.content.summary;
                    if (content && typeof content.summary === 'string') {
                        this.summary = content.summary.replace(/\n/g, '<br>');
                        // console.log('this.summary:', this.summary);
                    } else {
                        this.summary = null;
                    }
                    await this.getSummary();
                } else {
                    this.summary = null;
                }
            } catch (error) {
                this.summary = null;
                // 필요하다면 에러 메시지 출력
                // console.error(error);
            } finally {
                this.isLoading = false;
            }
        }
    }
}
</script>
