<script>
import axiosInstance from '@/axios_config/axiosConfig'
import { mapState } from 'vuex';

export default {
    name: 'liveChatLayout',
    computed: {
        ...mapState(['user', 'isConnectChzzk', 'isAuthenticated']),
        isLoggedIn() {
            return this.$store.state.user && this.$store.state.user.chzzk_user_info;
        },
        highlightTopChannelOrdered() {
            if (this.highlightTopChannel.length === 3) {
                return [this.highlightTopChannel[1], this.highlightTopChannel[0], this.highlightTopChannel[2]];
            }
            return this.highlightTopChannel;
        },
        highlightTopChannelRanks() {
            if (this.highlightTopChannel.length === 3) {
                return [2, 1, 3];
            }
            return this.highlightTopChannel.map((_, i) => i + 1);
        }
    },
    data() {
        const now = new Date();
        const yyyy = now.getFullYear();
        const mm = String(now.getMonth() + 1).padStart(2, '0');
        const dd = String(now.getDate()).padStart(2, '0');
        return {
            today: `${yyyy}.${mm}.${dd-1}일`,
            highlightTopChannel: []
        };
    },
    methods: {
        rankClass(idx) {
            if (idx === 1) return 'first-place';
            if (idx === 0) return 'second-place';
            if (idx === 2) return 'third-place';
            return '';
        },
        async getHighlightTopChannel() {
            const response = await axiosInstance.get(
                '/chat/analytics/highlight_top_channel'
            );

            if (response.data.status_code == 200) {
                this.highlightTopChannel = response.data.content.highlight_top_channel;
            } else {
                alert("오류 발생");
            }
        },
        goToAichatbot() {
            this.$router.push('/chatting');
        },
        goToChzzkConnect() {
            this.$router.push('/settings');
        }
    },
    mounted() {
        this.getHighlightTopChannel();
    }
}
</script>

