<script>
import axiosInstance from '@/axios_config/axiosConfig';
import { mapState } from 'vuex';

export default {
  name: "RouteAichatbot",
  data() {
    return {
      message: "",
      isRecording: false,
      showVoicePopup: false,
      messages: [],
      isSent: false,
      isTyping: false,
      mediaRecorder: null,
      audioChunks: [],
      recordingStatus: "마이크를 활성화하려면 설정에서 마이크 접근을 허용하세요",
      selectedFile: null,
      recordingTimeout: null,
      recognition: null,
      finalTranscript: "",
      access_token: "",
      ws: null,
      partialResponse: ""
    };
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    async AIPopup() {
      this.showVoicePopup = !this.showVoicePopup;
    },
    async getAccessToken() {
      try {
        const response = await axiosInstance.get("/chatbot/access_token");
        console.log('getAccessToken - ', response.data.content.access_token)
        if (response.data.status_code === 200) {
          this.access_token = response.data.content.access_token;
          this.connectToChatbot();
        } else {
          alert("허용되지 않은 사용자입니다.");
          // this.$router.push('/');
        }
      } catch (error) {
        console.error("액세스 토큰 가져오기 오류:", error);
        // this.$router.push('/');
      }
    },
    async connectToChatbot() {
      console.log('connectToChatbot - ', this.access_token)
      try {
        this.ws = new WebSocket(`wss://h0bin2.store/chatbot/websocket?access_token=${this.access_token}`);
        this.ws.onopen = () => {
          console.log("WebSocket connected");
          this.ws.send(JSON.stringify({ type: 'init' }));
        };
        this.ws.onmessage = (event) => {
          const data = JSON.parse(event.data);
          

          if (data.type === "start") {
            this.partialResponse = "";
            this.isTyping = true;
            this.messages.push({
              role: "bot",
              content: "",
              timestamp: new Date(),
              streaming: true
            });
            return;
          }

          if (data.type === "end") {
            const lastMessage = this.messages[this.messages.length - 1];
            if (lastMessage && lastMessage.streaming) {
              lastMessage.streaming = false;
              lastMessage.content = "";
              this.typeEffect(lastMessage, data.content);
            }
            this.isTyping = false;
            this.$nextTick(() => this.scrollToBottom());
            return;
          }

          if (data.action === "Final Answer" && data.action_input) {
            this.messages.push({ role: "bot", content: data.action_input });
          }
        };

        this.ws.onerror = () => {
          console.error("WebSocket 오류 발생");
          // this.$router.push('/');
        };
        this.ws.onclose = () => {
          console.warn("WebSocket 연결 종료됨");
        };
      } catch (error) {
        console.error("WebSocket 연결 실패:", error);
        // this.$router.push('/');
      }
    },
    async sendMessage() {
      // console.log('sendMessage 호출', new Date().toISOString(), this.message);
      if (!this.message.trim() || this.isTyping) return;

      this.isTyping = true;
      
      const messageText = this.message.trim();
      
      this.messages.push({ type: "chat", role: "user", content: messageText });
      this.message = "";
      this.isSent = true;
      this.$nextTick(() => {
        this.adjustTextareaHeight();
        this.scrollToBottom();
      });

      if (this.ws && this.ws.readyState === WebSocket.OPEN) {
        this.ws.send(JSON.stringify({ type: "chat", content: messageText }));
        this.isTyping = true;
      } else {
        console.error("WebSocket이 열려 있지 않습니다.");
      }
    },
    scrollToBottom() {
      this.$nextTick(() => {
        const chat = this.$refs.chatMessages;
        if (chat) chat.scrollTop = chat.scrollHeight;
      });
    },
    adjustTextareaHeight() {
      const textarea = this.$refs.messageInput;
      if (textarea) {
        textarea.style.height = "auto";
        textarea.style.height = textarea.scrollHeight + "px";
      }
    },
    typeEffect(messageObj, fullText) {
      let i = 0;
      const typing = () => {
        if (i <= fullText.length) {
          messageObj.content = fullText.slice(0, i);
          i++;
          setTimeout(typing, 30); // 30ms마다 한 글자씩
          this.$nextTick(() => this.scrollToBottom());
        }
      };
      typing();
    },
    toggleRecording() {
      if (this.isRecording) {
        this.stopSpeechRecognition();
      } else {
        this.startSpeechRecognition();
      }
    },
    startSpeechRecognition() {
      if (!('webkitSpeechRecognition' in window) && !('SpeechRecognition' in window)) {
        this.recordingStatus = '이 브라우저는 음성 인식을 지원하지 않습니다.';
        return;
      }
      const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
      this.recognition = new SpeechRecognition();
      this.recognition.lang = 'ko-KR';
      this.recognition.continuous = false;
      this.recognition.interimResults = false;
      this.recognition.onstart = () => {
        this.isRecording = true;
        this.recordingStatus = '음성 인식 중...';
      };
      this.recognition.onresult = (event) => {
        const transcript = event.results[0][0].transcript;
        this.recordingStatus = '인식 완료: ' + transcript;
        this.isRecording = false;
        this.sendVoiceMessage(transcript);
      };
      this.recognition.onerror = (event) => {
        this.recordingStatus = '음성 인식 오류: ' + event.error;
        this.isRecording = false;
      };
      this.recognition.onend = () => {
        if (this.isRecording) {
          this.isRecording = false;
          this.recordingStatus = '음성 인식이 종료되었습니다.';
        }
      };
      this.recognition.start();
    },
    stopSpeechRecognition() {
      if (this.recognition) {
        this.recognition.stop();
        this.isRecording = false;
        this.recordingStatus = '마이크가 꺼졌습니다.';
      }
    },
    sendVoiceMessage(transcript) {
      if (!transcript) return;
      this.message = transcript;
      this.sendMessage();
    },
  },
  async created() {
    await this.getAccessToken();
  },
  mounted() {
    this.adjustTextareaHeight();
    this.scrollToBottom();
  },
  beforeUnmount() {
    if (this.recognition) this.recognition.stop();
    if (this.ws) this.ws.close();
  }
};
</script>

