<template>
    <div class="select-wrapper">
        <select v-model="selected" class="live-select" size="1">
            <option value="">라이브 이력 선택</option>
            <option :selected="selectedLiveId === live.live_key" v-for="live in liveList" :key="live.session_start_live_id + '_' + live.session_end_live_id" :value="live.session_start_live_id + '_' + live.session_end_live_id">
                {{ formatDateWithDay(live.session_start_time) }}
            </option>
        </select>
    </div>
</template>
<script>
export default {
    name: 'SelectLive',
    props: {
        liveList: Array,
        defaultTimeUnit: {
            type: String,
            default: 'hour'
        },
        selectedLiveId: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            selected: '',
            selectedTimeUnit: this.defaultTimeUnit
        }
    },
    watch: {
        selected(newVal) {
            this.$emit('selected', newVal);
        },
        selectedTimeUnit(newVal) {
            this.$emit('timeunit', newVal);
        }
    },
    emits:['selected','timeunit'],
    methods: {
        formatTime(timeStr) {
            if (!timeStr) return '-';
            // YYYY-MM-DD HH:mm:ss → YYYY.MM.DD
            return timeStr.replace(/-/g, '.').slice(0, 10);
        },
        formatDateWithDay(dateStr) {
            const date = new Date(dateStr);
            const days = ['일', '월', '화', '수', '목', '금', '토'];
            const y = date.getFullYear();
            const m = ('0' + (date.getMonth() + 1)).slice(-2);
            const d = ('0' + date.getDate()).slice(-2);
            const day = days[date.getDay()];
            return `${y}-${m}-${d}(${day})`;
        }
    }
}
</script>
