<script>
import inputText from '@/common/inputText.vue';
import aImgTag from '@/common/aImgTag.vue';
import axiosInstance from '@/axios_config/axiosConfig';

export default {
    name:'topHeader',
    components:{
        inputText,
        aImgTag
    },
    data(){
        return {
            isInputFocus: false,
            searchValue: '',
            autoResults: [],
            userHashId: ''
        }
    },
    props:{
        isLoggedIn:{
            type: Boolean,
            required: true
        },
        user:{
            type: Object,
            default: () => ({})
        }
    },
    methods:{
        logUser(){
            console.log(this.user);
        },
        getUserInfo() {
            // console.log("this.user : ", this.user);
            // const cookies = document.cookie.split(';');
            // const hashedUserIdCookie = cookies.find(cookie => cookie.trim().startsWith('hashed_userid='));
            
            // if (this.user.chzzk_use) {
            //     this.userHashId = this.user.chzzk_user_info.content.channelId;
            // }
        },
        async onInputText(value) {
            if (!value) {
                this.autoResults = [];
                return;
            }
            try {
                const res = await axiosInstance.get(`/chzzk/search/lounges/auto-complete?keyword=${encodeURIComponent(value)}`);
                // 응답 구조에 맞게 추출
                this.autoResults = (
                  res.data?.content?.content?.data || []
                );
            } catch (e) {
                this.autoResults = [];
            }
        },
        onBlur() {
            setTimeout(() => { this.isInputFocus = false; }, 150);
        },
        selectItem(item) {
            this.searchValue = item;
            this.isInputFocus = false;
            // 필요시 emit 등으로 부모에 선택값 전달
            this.searchChannels(item);
        },
        async searchChannels(value) {
            if (!value) {
                this.autoResults = [];
                return;
            }
            const res = await axiosInstance.get(`/chzzk/search/channels?keyword=${encodeURIComponent(value)}`);
            if (res.data.status_code == 200) {
                this.channelId = res.data.content.content.data[0].channel.channelId;
                console.log(this.channelId);
                this.$router.push(`/studio/${this.channelId}`);
            } else {
                this.autoResults = [];
            }
        }
    },
    watch: {
        // isLoggedIn(newVal) {
        //     if (newVal) {
        //         this.getUserInfo();
        //     }
        // }
    }
}
</script>
