<template>
    <div class="pw-change-page">
      <div class="pw-change-page-container">
        <div class="pw-change-container">
          <h1 class="pw-change-title">비밀번호 변경</h1>
          <div class="pw-change-subtitle">
            <p>비밀번호를 변경하고 싶으신가요?</p>
          </div>
          
          <form id="passwordChangeForm" @submit.prevent="handlePasswordChange">
            <div class="pw-change-input-section">
              <div class="pw-change-input-group">
                <input 
                  type="password" 
                  id="newpassword" 
                  v-model="newpassword" 
                  placeholder="새로운 비밀번호를 입력해주세요." 
                  required
                  @focus="showPasswordRequirements = true"
                  @blur="showPasswordRequirements = false"
                  :class="{ 'pw-change-error': !isPasswordValid && newpassword !== '', 'pw-change-valid': isPasswordValid && newpassword !== '' }"
                  class="pw-change-input"
                >
                <div class="pw-change-requirements" :class="{ 'pw-change-show': showPasswordRequirements && newpassword !== '' }">
                  <div class="pw-change-requirement" :class="{ 'pw-change-valid-req': isLengthValid }">
                    <span class="pw-change-requirement-icon">{{ isLengthValid ? '✓' : '✕' }}</span>
                    <span>최소 8자를 포함해야 합니다</span>
                  </div>
                  <div class="pw-change-requirement" :class="{ 'pw-change-valid-req': isCharsValid }">
                    <span class="pw-change-requirement-icon">{{ isCharsValid ? '✓' : '✕' }}</span>
                    <span>대문자, 소문자, 숫자를 포함해야 합니다</span>
                  </div>
                  <div class="pw-change-requirement" :class="{ 'pw-change-valid-req': isSpecialValid }">
                    <span class="pw-change-requirement-icon">{{ isSpecialValid ? '✓' : '✕' }}</span>
                    <span>12자 미만인 경우 특수 문자를 포함해야 합니다</span>
                  </div>
                </div>
              </div>
              
              <div class="pw-change-input-group">
                <input 
                  type="password" 
                  id="confirm-password" 
                  v-model="confirmPassword" 
                  placeholder="비밀번호를 다시 입력해주세요."
                  required
                  :class="{ 'pw-change-error': !passwordsMatch && confirmPassword !== '', 'pw-change-valid': passwordsMatch && confirmPassword !== '' }"
                  class="pw-change-input"
                >
              </div>
            </div>
            
            <button type="submit" :disabled="!isFormValid" class="pw-change-button">비밀번호 변경하기</button>
          </form>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import axiosInstance from '@/axios_config/axiosConfig';
  
  export default {
    data() {
      return {
        newpassword: '',
        confirmPassword: '',
        showPasswordRequirements: false
      };
    },
    
    computed: {
      isLengthValid() {
        return this.newpassword.length >= 8;
      },
      
      isCharsValid() {
        return /[A-Z]/.test(this.newpassword) && 
               /[a-z]/.test(this.newpassword) && 
               /[0-9]/.test(this.newpassword);
      },
    
      isSpecialNeeded() {
        return this.newpassword.length < 12;
      },
      
      hasSpecialChar() {
        // eslint-disable-next-line no-useless-escape
        return /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/.test(this.newpassword);
      },
      
      isSpecialValid() {
        return !this.isSpecialNeeded || this.hasSpecialChar;
      },
      
      isPasswordValid() {
        if (this.newpassword === '') return false;
        return this.isLengthValid && this.isCharsValid && this.isSpecialValid;
      },
      
      passwordsMatch() {
        return this.newpassword !== '' && this.newpassword === this.confirmPassword;
      },
      
      isFormValid() {
        return this.isPasswordValid && this.passwordsMatch;
      }
    },
    
    methods: {
        async handlePasswordChange() {
            if (!this.isFormValid) {
                alert('비밀번호 요구사항을 충족해야 합니다.');
                return;
            }

            if (confirm('비밀번호를 변경하시겠습니까?')) {
                try {
                    const response = await axiosInstance.post('/user/password-change', {
                        new_password: this.newpassword
                    });

                    if (response.data.status_code === 200) {
                        alert('비밀번호가 성공적으로 변경되었습니다.');
                        this.newpassword = '';
                        this.confirmPassword = '';
                        this.$router.push('/settings');
                    } else {
                        alert('비밀번호 변경에 실패했습니다.');
                    }
                } catch (error) {
                    console.error('비밀번호 변경 에러:', error);
                    alert('비밀번호 변경에 실패했습니다.');
                }
            }
        }
    }
  };
  </script>
  
